// Basic pricing logic composable
import { PERIODS } from '@config/pricingConfig'
import { plansBasic } from '@data/plans'

export function usePricingBasic() {
  /**
   * Calculate plan based on basic pricing logic
   * @param {Object} state - Current pricing state
   * @returns {Object} - Calculated plan result
   */
  function calculatePlan(state) {
    const plans = plansBasic

    // Find suitable plan that covers all requirements
    let suitablePlan = plans.find(
      (plan) =>
        plan.maxSocialAccounts >= state.socialAccounts &&
        plan.maxUsers >= state.users &&
        plan.maxSources >= state.sources
    )

    // Safety fallback if needs exceed the highest plan
    if (!suitablePlan) {
      suitablePlan = plans[plans.length - 1]
    }

    // Calculate final price and labels
    let finalPrice = 0
    let priceLabel = '/mois'
    let savingsBadge = null
    let offerText = null

    switch (state.period) {
      case PERIODS.MONTHLY:
        finalPrice = suitablePlan.priceMonthly
        priceLabel = '/mois'
        break

      case PERIODS.ONE_YEAR:
        finalPrice = suitablePlan.priceMonthly * 10
        priceLabel = ''
        savingsBadge = 'Économisez 17%'
        offerText = '2 mois OFFERTS'
        break

      case PERIODS.TWO_YEARS:
        finalPrice = suitablePlan.priceMonthly * 9 * 2
        priceLabel = ''
        savingsBadge = 'Économisez 25%'
        offerText = '6 mois OFFERTS'
        break
    }

    return {
      plan: suitablePlan,
      finalPrice: Math.round(finalPrice),
      priceLabel,
      savingsBadge,
      offerText,
      displayValues: {
        socialAccounts: suitablePlan.maxSocialAccounts,
        users: suitablePlan.maxUsers,
        sources: suitablePlan.maxSources
      },
      extraCosts: {
        social: null,
        users: null,
        sources: null
      },
      isCustomPlan: false,
      moderationIncluded: suitablePlan.moderationIncluded
    }
  }

  return {
    calculatePlan
  }
}
