// Advanced pricing logic composable
import { PERIODS } from '@config/pricingConfig'
import { EXTRA_COSTS_YEARLY, UPGRADE_THRESHOLD } from '@data/extraCosts'
import { plans } from '@data/plans'

export function usePricingAdvanced() {
  /**
   * Calculate plan based on advanced pricing logic with extra costs
   * @param {Object} state - Current pricing state
   * @returns {Object} - Calculated plan result
   */
  function calculatePlan(state) {
    let finalPlan = null
    let displayPrice = 0
    let priceLabel = '/mois'
    let isCustomPlan = false
    let savingsBadge = null
    let offerText = null

    // Monthly period (simple tiers)
    if (state.period === PERIODS.MONTHLY) {
      // Find the first plan that covers ALL current needs
      finalPlan = plans.find(
        (p) =>
          p.maxSocialAccounts >= state.socialAccounts &&
          p.maxUsers >= state.users &&
          p.maxSources >= state.sources
      )

      // Safety fallback if needs exceed the highest plan
      if (!finalPlan) finalPlan = plans[plans.length - 1]

      displayPrice = finalPlan.priceMonthly
      priceLabel = '/mois'
    } else {
      // Yearly / 2-year period (custom pricing with extra costs)
      priceLabel = ''

      // Price multiplier (10 months paid for 1 year, 18 months for 2 years)
      const multiplier = state.period === PERIODS.ONE_YEAR ? 10 : 18

      // Set promotional badges
      if (state.period === PERIODS.ONE_YEAR) {
        savingsBadge = 'Économisez 17%'
        offerText = '2 mois OFFERTS'
      } else {
        savingsBadge = 'Économisez 25%'
        offerText = '6 mois OFFERTS'
      }

      // Find the "Ceiling" plan that covers everything natively
      let ceilingPlanIndex = plans.findIndex(
        (p) =>
          p.maxSocialAccounts >= state.socialAccounts &&
          p.maxUsers >= state.users &&
          p.maxSources >= state.sources
      )
      if (ceilingPlanIndex === -1) ceilingPlanIndex = plans.length - 1
      const ceilingPlan = plans[ceilingPlanIndex]

      // Compare Ceiling Plan vs Lower Plan + Options
      if (ceilingPlanIndex === 0) {
        // Already on the lowest plan (Freelance), no comparison possible
        finalPlan = ceilingPlan
        displayPrice = ceilingPlan.priceMonthly * multiplier
      } else {
        // The base plan for custom calculation is the one right below the ceiling
        const basePlan = plans[ceilingPlanIndex - 1]

        // Calculate ceiling scenario total yearly price
        const ceilingTotalYearly = ceilingPlan.priceMonthly * multiplier

        // Calculate custom scenario (Base + Yearly Options)
        const extraSocial = Math.max(0, state.socialAccounts - basePlan.maxSocialAccounts)
        const extraUsers = Math.max(0, state.users - basePlan.maxUsers)
        const extraSources = Math.max(0, state.sources - basePlan.maxSources)

        const optionsTotalYearly =
          extraSocial * EXTRA_COSTS_YEARLY.social +
          extraUsers * EXTRA_COSTS_YEARLY.users +
          extraSources * EXTRA_COSTS_YEARLY.sources

        const customTotalYearly = basePlan.priceMonthly * multiplier + optionsTotalYearly

        // Decision based on threshold and special freelance rules
        const forceUpgradeToStarter =
          basePlan.id === 'freelance' &&
          state.socialAccounts >= ceilingPlan.maxSocialAccounts &&
          state.users >= ceilingPlan.maxUsers &&
          state.sources >= ceilingPlan.maxSources

        if (!forceUpgradeToStarter && customTotalYearly <= ceilingTotalYearly - UPGRADE_THRESHOLD) {
          // Use base plan with custom pricing
          finalPlan = basePlan
          displayPrice = customTotalYearly
          isCustomPlan = true
        } else {
          // Use ceiling plan (forced upgrade or economical choice)
          finalPlan = ceilingPlan
          displayPrice = ceilingTotalYearly
          isCustomPlan = false
        }
      }
    }

    // Calculate display values (always show user-selected values unless plan offers more)
    const displayValues = {
      socialAccounts: Math.max(finalPlan.maxSocialAccounts, state.socialAccounts),
      users: Math.max(finalPlan.maxUsers, state.users),
      sources: Math.max(finalPlan.maxSources, state.sources)
    }

    // Calculate extra costs display
    const extraCosts = {
      social: null,
      users: null,
      sources: null
    }

    // Show extra costs in yearly modes or when using custom plan
    if (state.period !== PERIODS.MONTHLY) {
      if (isCustomPlan) {
        // Show extra cost texts only for values exceeding plan limits
        if (state.socialAccounts > finalPlan.maxSocialAccounts) {
          extraCosts.social = ` (+${EXTRA_COSTS_YEARLY.social}€/page)`
        }
        if (state.users > finalPlan.maxUsers) {
          extraCosts.users = ` (+${EXTRA_COSTS_YEARLY.users}€/user)`
        }
        if (state.sources > finalPlan.maxSources) {
          extraCosts.sources = ` (+${EXTRA_COSTS_YEARLY.sources}€/source)`
        }
      } else {
        // Always show extra cost information in yearly mode
        extraCosts.social = ` (+${EXTRA_COSTS_YEARLY.social}€/page)`
        extraCosts.users = ` (+${EXTRA_COSTS_YEARLY.users}€/user)`
        extraCosts.sources = ` (+${EXTRA_COSTS_YEARLY.sources}€/source)`
      }
    }

    return {
      plan: finalPlan,
      finalPrice: Math.round(displayPrice),
      priceLabel,
      savingsBadge,
      offerText,
      displayValues,
      extraCosts,
      isCustomPlan,
      moderationIncluded: finalPlan.moderationIncluded
    }
  }

  return {
    calculatePlan
  }
}
