<template>
  <aside id="result-card">
    <div class="card">
      <div class="card-header">Votre sélection</div>
      <div class="card-body">
        <div class="plan-header">
          <h2>{{ result.plan.name }}</h2>
          <div class="offer-container">
            <span v-if="result.savingsBadge" class="savings-badge">
              {{ result.savingsBadge }}
            </span>
            <div v-if="result.offerText" class="offer-text">
              {{ result.offerText }}
            </div>
          </div>
        </div>

        <div class="price-display">
          <span class="price">{{ result.finalPrice }}€</span>
          <span class="period">&nbsp;{{ result.priceLabel }}</span>
        </div>

        <ul class="features-list">
          <li>
            Jusqu'à
            <span class="feature-value">{{
              result.displayValues.socialAccounts
            }}</span>
            pages connectées
            <span v-if="result.extraCosts.social" class="extra-cost-text">
              {{ result.extraCosts.social }}
            </span>
          </li>
          <li>
            Jusqu'à
            <span class="feature-value">{{ result.displayValues.users }}</span>
            utilisateurs
            <span v-if="result.extraCosts.users" class="extra-cost-text">
              {{ result.extraCosts.users }}
            </span>
          </li>
          <li>
            Poster depuis
            <span class="feature-value">{{
              result.displayValues.sources
            }}</span>
            sources
            <span v-if="result.extraCosts.sources" class="extra-cost-text">
              {{ result.extraCosts.sources }}
            </span>
          </li>
          <li
            :class="{ 'not-included': !result.moderationIncluded }"
            class="moderation-feature"
          >
            Modération des réseaux
          </li>
        </ul>

        <button class="cta-btn" @click="redirectToSaaS">Choisir ce plan</button>
      </div>
    </div>
  </aside>
</template>

<script>
export default {
  name: "ResultCard",

  props: {
    result: {
      type: Object,
      required: true,
      validator: (value) => {
        return (
          value &&
          typeof value === "object" &&
          value.plan &&
          typeof value.finalPrice === "number" &&
          typeof value.priceLabel === "string" &&
          value.displayValues &&
          typeof value.moderationIncluded === "boolean"
        )
      },
    },
  },

  methods: {
    // Redirect to SaaS with plan ID as query parameter
    redirectToSaaS() {
      const planId = this.result.plan.id
      const saasUrl = `https://saas.mashup-web.com/#/?plan=${planId}`
      window.location.href = saasUrl
    },
  },
}
</script>

